.process {
  margin: 0 2vw;
  height: 754px;
  padding-top: 148px;

  &__wrapper {
    max-width: 1024px;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
  }

  &__block-text {
    max-width: 362px;

    margin-top: 132px;
  }

  &__decor-text {
    @extend %decor-text;
    color: $pseudo-color;
  }

  &__subtitle {
    margin-bottom: 24px;

    font-weight: 600;
    font-size: 32px;
    line-height: 42px;

    color: $main-subtitle;
  }

  &__text {
    font-family: $font-opens;
    font-weight: normal;
    font-size: $text-14;
    line-height: 21px;

    color: $main-text;
  }

  &__services {
    max-width: 560px;

    display: grid;
    grid-template-columns: repeat(auto-fill, 264px);
    grid-template-rows: repeat(auto-fill, 264px);
    grid-gap: 30px;

    z-index: 1;
  }
}
